import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Link, Route } from 'react-router-dom';
import { Helmet } from "react-helmet";
import { Layout, Row, Alert, Typography } from 'antd';
import 'antd/dist/antd.css';
import Loader from 'react-loader-spinner';
import { SearchBox, ItemContainer, MovieDetails } from './components';
import Pagination from "react-js-pagination";
import { connect } from 'react-redux';
import { TR, } from '../lang/types';
import { tr, en } from './lang';

require("./components/pagination.css");



const API_KEY = 'b1fc3699';
const { Header, Content, Footer } = Layout;
const TextTitle = Typography.Title;



function Movies(props) {


  const [data, setData] = useState(null);
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(false);
  const [q, setQuery] = useState('Pokemon');
  const [p, setPage] = useState(1);
  const [totalResults, setTotalResults] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const [postsPerPage] = useState(10);
  const [activeLink, setActiveLink] = useState(1);
  const [activateModal, setActivateModal] = useState(false);
  const [detail, setShowDetail] = useState(false);
  const [detailRequest, setDetailRequest] = useState(false);


  useEffect(() => {

    setLoading(true);
    setError(null);
    setData(null);

    fetch(`https://www.omdbapi.com/?s=${q}&page=${p}&apikey=${API_KEY}`)
      .then(resp => resp)
      .then(resp => resp.json())
      .then(response => {
        if (response.Response === 'False') {
          setError(response.Error);
        }
        else {
          setData(response.Search.sort((a, b) => (a.Year > b.Year) ? 1 : -1));
          setTotalResults(response.totalResults);
        }


        setLoading(false);
      })
      .catch(({ message }) => {
        setError(message);
        setLoading(false);
      })


  }, [q, p]);

  const [text, setText] = useState(en);

  useEffect(() => {
    if (props.lang === TR) {
      setText(tr);
    }
    else { setText(en); }
  }, [props.lang,]);

  const indexOfLastPost = currentPage * postsPerPage;
  const indexOfFirstPost = indexOfLastPost - postsPerPage;

  const currentPosts = data ? data.slice(indexOfFirstPost, indexOfLastPost) : null;

  const paginate = pageNumber => {
    setActiveLink(pageNumber);
    if (postsPerPage < 10) {
      if ((pageNumber % 2) === 1) {
        setPage((pageNumber / 2) + 1); pageNumber = 1
      }
      else {
        setPage(pageNumber / 2);
        pageNumber = 2
      }
      setCurrentPage(pageNumber);
    }
    setPage(pageNumber);

  };

  return (

    <div className="App">
      <Helmet>
        <title>{text.movies}</title>
      </Helmet>

      <Layout className="layout">
        <Header>
          <div style={{ textAlign: 'center' }}>
            <TextTitle style={{ color: '#ffffff', marginTop: '14px' }} level={3}>{text.movies}</TextTitle>
          </div>
        </Header>

        <Router>
          <Route
            path="/"
            exact
            render={() =>

              <Content style={{ padding: '0 50px' }}>
                <div style={{ background: '#fff', padding: 24, minHeight: 280 }}>
                  <SearchBox searchHandler={setQuery} defaultValue={q} setPage={setPage} setCurrentPage={setCurrentPage} setActiveLink={setActiveLink} Lang={props.lang} />
                  <br />

                  <Row gutter={16} type="flex" justify="center">
                    {loading &&
                      <Loader />
                    }

                    {error !== null &&
                      <div style={{ margin: '20px 0' }}>
                        <Alert message={error} type="error" />
                      </div>
                    }

                    {currentPosts !== null && currentPosts.length > 0 && currentPosts.map((result, index) => (
                      <div key={index}>
                        <Link to="/movies/details">
                          <ItemContainer
                            ShowDetail={setShowDetail}
                            DetailRequest={setDetailRequest}
                            ActivateModal={setActivateModal}
                            Lang={props.lang}
                            key={index}
                            {...result}
                            API_KEY={API_KEY}
                          />
                        </Link>
                      </div>
                    ))}
                  </Row>

                  {
                    data !== null && data.length > 0 &&
                    <Pagination
                      activePage={activeLink}
                      itemsCountPerPage={postsPerPage}
                      totalItemsCount={parseInt(totalResults)}
                      pageRangeDisplayed={5}
                      onChange={paginate}
                      itemClass="item"
                      linkClass="link"
                      activeLinkClass="activeLink"
                    />
                  }
                </div>
              </Content>
            }
          />
          <Route
            path="/movies/details"
            render={() =>
              <div> {detailRequest === false ?
                (<MovieDetails {...detail} Lang={props.lang} />) :
                (<Loader />)
              } </div>}
          />
        </Router>
        <Footer style={{ textAlign: 'center' }}><a href='https://github.com/ibrahimAKIN'>github.com/ibrahimAKIN</a></Footer>
      </Layout>

    </div >
  );
}

const mapStateToProps = ({ response }) => {
  const { lang } = response;
  return { lang };
}

export default connect(mapStateToProps, {})(Movies);
