import React, { useEffect, useState } from 'react';
import { /*BrowserRouter as Router,*/ HashRouter, Route, Redirect /*Switch*/ } from 'react-router-dom';
import Nav from '../components/Nav';
import LatestPoint from '../projects/latest_point';
import LogInApp from '../projects/login_app';
import OMR from '../projects/omr';
import PikoIsland from '../projects/piko_island';
import Tedbir from '../projects/tedbir';
import UAV from '../projects/uav';
import Movies from '../moviesSPA';
import Home from '../pages/home';
import About from '../pages/about';
import Shop from '../pages/shop';



const Navigation = (props) => {

    useEffect(() => {
        console.log(window.location);
        if (window.location.pathname == "/movies/details") {
            window.location.pathname = "";
            window.location.hash = "/movies";
            //setMovieDetails(true);
        }
    });

    return (
        <HashRouter basename='/'>
            <div className="App" >
                <Nav />
                {/*<Switch>*/}
                <Route exact path="/" component={Home} />
                <Route path="/movies" component={Movies} />
                <Route path="/latest_point" component={LatestPoint} />
                <Route path="/login_app" component={LogInApp} />
                <Route path="/omr" component={OMR} />
                <Route path="/piko_island" component={PikoIsland} />
                <Route path="/tedbir" component={Tedbir} />
                <Route path="/uav" component={UAV} />
                <Route path="/about" component={About} />
                <Route path="/shop" component={Shop} />
                {/*</Switch>*/}
            </div>
        </HashRouter>


    )
}


export default Navigation;
